import React, { useState, useEffect } from "react";
import { User, Settings, LogOut, Shield, CloudUpload, RefreshCw, LogIn, Cloud, DownloadCloud } from "lucide-react";
import { useFirebaseAuth } from "@/contexts/FirebaseAuthContext";
import { useShift } from "@/contexts/ShiftContext";
import { useNavigate } from "react-router-dom";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Button } from "@/components/ui/button";
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog";
import { ChangePasswordDialog } from "./ChangePasswordDialog";
import { getAvatarById } from "@/utils/avatars";
import { backupToGoogleDriveWithUiToken, restoreFromGoogleDriveWithUiToken, signInWithGoogleForBackupGis, signInWithGoogleForBackup, getGoogleAccessTokenInfo, startServerGoogleOauth, pollForServerGoogleToken, fetchStoredRefreshToken, saveGoogleBackupToken } from "@/utils/googleDriveBackup";
import { auth } from "@/firebase";
import { useToast } from "@/hooks/use-toast";
import { FreeTrialService } from "@/utils/freeTrialService";
import { listenDesktopUpdates, selectDesktopUrl } from "@/utils/appDownloadService";

interface ProfileIconProps {
  className?: string;
}

export function ProfileIcon({ className }: ProfileIconProps) {
  const { user, profile, signOut, updateUserProfile } = useFirebaseAuth();
  const navigate = useNavigate();
  const [showChangePassword, setShowChangePassword] = useState(false);
  const { isShiftActive } = useShift();
  const { toast } = useToast();
  const [isBackingUp, setIsBackingUp] = useState(false);
  const [isRestoring, setIsRestoring] = useState(false);
  const [isGoogleSigning, setIsGoogleSigning] = useState(false);
  const [{ token: googleToken }, setGoogleTokenInfo] = useState(() => getGoogleAccessTokenInfo());

  type ElectronAPI = {
    onUpdateAvailable: (cb: (payload: unknown) => void) => void;
    onUpdateDownloaded: (cb: (payload: unknown) => void) => void;
    onUpdateError: (cb: (payload: any) => void) => void;
    onUpdateProgress?: (cb: (payload: { percent?: number|null; downloaded?: number; total?: number }) => void) => void;
    checkUpdate: () => Promise<unknown>;
    downloadUpdate: () => Promise<unknown>;
    applyUpdate: () => Promise<unknown>;
    openUpdateFolder?: () => Promise<unknown>;
    appVersion?: () => Promise<string>;
  };
  const electronAPI = (typeof window !== 'undefined' ? (window as unknown as { electronAPI?: ElectronAPI }).electronAPI : undefined);
  const isElectron = !!electronAPI;
  const [updateAvailable, setUpdateAvailable] = useState<boolean>(false);
  const [updateDialogOpen, setUpdateDialogOpen] = useState<boolean>(false);
  const [updateVersion, setUpdateVersion] = useState<string>('');
  const [updateNotes, setUpdateNotes] = useState<string>('');
  const [updateProgress, setUpdateProgress] = useState<number>(0);
  const [isDownloadingUpdate, setIsDownloadingUpdate] = useState<boolean>(false);
  const [updateDownloadUrl, setUpdateDownloadUrl] = useState<string>("");
  const [currentVersion, setCurrentVersion] = useState<string>("");
  const [autoApplied, setAutoApplied] = useState<boolean>(false);
  
  const [freeTrialHoursRemaining, setFreeTrialHoursRemaining] = useState(0);
  const [isOnFreeTrial, setIsOnFreeTrial] = useState(false);

  useEffect(() => {
    const checkTrial = async () => {
      if (!user?.uid) return;
      const trialValidity = await FreeTrialService.checkTrialValidity(user.uid);
      setIsOnFreeTrial(trialValidity.isValid && !trialValidity.isExpired);
      setFreeTrialHoursRemaining(trialValidity.hoursRemaining);
    };
    checkTrial();
  }, [user?.uid]);

  const compareVersions = (a?: string, b?: string): number => {
    const pa = String(a || '').split('.').map((x) => parseInt(x, 10) || 0);
    const pb = String(b || '').split('.').map((x) => parseInt(x, 10) || 0);
    for (let i = 0; i < Math.max(pa.length, pb.length); i++) {
      const da = pa[i] || 0; const db = pb[i] || 0;
      if (da > db) return 1; if (da < db) return -1;
    }
    return 0;
  };

  useEffect(() => {
    if (!isElectron || !electronAPI) return;
    try {
      electronAPI.onUpdateAvailable((payload: { source?: string; info?: { version?: string; updateUrl?: string; notes?: string } }) => {
        const v = String(payload?.info?.version || '');
        setUpdateVersion(v);
        setUpdateNotes(String(payload?.info?.notes || ''));
        if (compareVersions(v, currentVersion) > 0) {
          setUpdateAvailable(true);
        } else {
          setUpdateAvailable(false);
        }
      });
      electronAPI.onUpdateDownloaded((payload: { source?: string; info?: { version?: string; updateUrl?: string; notes?: string } }) => {
        const v = String(payload?.info?.version || '');
        setUpdateVersion(v);
        setUpdateNotes(String(payload?.info?.notes || ''));
        setIsDownloadingUpdate(false);
        setUpdateProgress(100);
        if (compareVersions(v, currentVersion) > 0) {
          setUpdateAvailable(true);
        } else {
          setUpdateAvailable(false);
        }
        try { if (isElectron && user?.uid && v) { (updateUserProfile as any)?.({ lastAckDesktopVersion: v }); } } catch {}
      });
      electronAPI.onUpdateError((payload: any) => {
        try { toast({ title: 'فشل التحديث', description: String(payload?.message || payload?.code || 'تعذر التحميل'), variant: 'destructive' }); } catch {}
      });
      if (typeof electronAPI.onUpdateProgress === 'function') {
        electronAPI.onUpdateProgress((payload) => {
          const p = (payload?.percent == null ? 0 : Number(payload.percent));
          setIsDownloadingUpdate(true);
          setUpdateProgress(Math.max(0, Math.min(100, isNaN(p) ? 0 : p)));
        });
      }
      if (typeof electronAPI.appVersion === 'function') {
        electronAPI.appVersion().then((ver) => {
          if (typeof ver === 'string') setCurrentVersion(ver);
          try { if (isElectron && user?.uid && typeof ver === 'string' && ver) { (updateUserProfile as any)?.({ desktopInstalledVersion: ver }); } } catch {}
        }).catch(() => {});
      }
    } catch (e) { void e; }
  }, [isElectron, electronAPI]);

  useEffect(() => {
    if (!isElectron) return;
    if (updateProgress >= 100) {
      setUpdateDialogOpen(true);
    }
  }, [isElectron, updateProgress]);

  useEffect(() => {
    const unsub = listenDesktopUpdates((data) => {
      const v = String(data?.version || "");
      const uidVal = String(user?.uid || 'anon');
      const seenKey = `desktop_update_seen_${uidVal}`;
      let seen = '';
      try { seen = localStorage.getItem(seenKey) || ''; } catch {}
      if (v && v !== updateVersion) {
        if (compareVersions(v, currentVersion) > 0) {
          if (seen === v) {
            setUpdateAvailable(false);
            setUpdateDialogOpen(false);
          } else {
            setUpdateAvailable(true);
            setUpdateDialogOpen(true);
            try { localStorage.setItem(seenKey, v); } catch {}
          }
        } else {
          setUpdateAvailable(false);
        }
        setUpdateVersion(v);
        setUpdateNotes(String(data?.notes || ""));
        const preferred = selectDesktopUrl({ downloadURL_64: data?.downloadURL_64, downloadURL_32: data?.downloadURL_32 }) || "";
        setUpdateDownloadUrl(preferred);
      }
    });
    return () => { try { unsub(); } catch {} };
  }, [updateVersion, currentVersion, user?.uid]);

  const handleConfirmUpdate = async () => {
    try {
      if (electronAPI) {
        try { await electronAPI.checkUpdate(); } catch {}
        setUpdateDialogOpen(true);
        setIsDownloadingUpdate(true);
        setUpdateProgress(0);
        const res = await (electronAPI as any).downloadUpdate?.(updateDownloadUrl) ?? await electronAPI.downloadUpdate();
        if (typeof res === 'string' && res.startsWith('err:')) {
          try { toast({ title: 'فشل التحديث', description: res.slice(4), variant: 'destructive' }); } catch {}
          setIsDownloadingUpdate(false);
        } else if (res === 'noop') {
          try { toast({ title: 'لا يوجد تحديث', description: 'لا تتوفر نسخة أحدث حالياً.' }); } catch {}
        } else if (res === 'manual') {
          try { toast({ title: 'جارٍ تنزيل التحديث', description: 'سيتم تطبيق التحديث تلقائيًا بعد اكتمال التنزيل.' }); } catch {}
        } else if (res === 'autoUpdater') {
          try { toast({ title: 'تنزيل تلقائي', description: 'سيتم تطبيق التحديث تلقائياً بواسطة النظام.' }); } catch {}
        }
      }
    } catch (e) {
      try { toast({ title: 'فشل التحديث', description: (e as any)?.message || 'حدث خطأ أثناء التنزيل', variant: 'destructive' }); } catch {}
    }
  };

  const handleApplyUpdate = async () => {
    try {
      if (electronAPI) {
        const res = await electronAPI.applyUpdate();
        if (typeof res === 'string' && res.startsWith('err:')) {
          try { toast({ title: 'فشل تطبيق التحديث', description: res.slice(4), variant: 'destructive' }); } catch {}
        }
      }
    } catch (e) {
      try { toast({ title: 'فشل تطبيق التحديث', description: (e as any)?.message || 'حدث خطأ أثناء تطبيق التحديث', variant: 'destructive' }); } catch {}
    }
  };


  // استخراج الأحرف الأولى من الاسم أو البريد الإلكتروني
  const getInitials = () => {
    if (profile?.fullName) {
      return profile?.fullName
        .split(" ")
        .map((name) => name.charAt(0))
        .join("")
        .toUpperCase()
        .slice(0, 2);
    }
    if (user.email) {
      return user.email.charAt(0).toUpperCase();
    }
    return "U";
  };

  // الحصول على صورة البروفيل الكرتونية
  const selectedAvatar = profile?.avatarId ? getAvatarById(profile.avatarId) : null;

  // مراقبة تغييرات البروفيل لإعادة التحديث
  useEffect(() => {
    console.log('Profile updated in ProfileIcon:', profile?.avatarId);
  }, [profile?.avatarId]);

  // التقط التوكن من نافذة الموافقة (cashy.link) عبر postMessage إن كانت مُهيأة على الخادم
  useEffect(() => {
    const handler = async (evt: MessageEvent) => {
      try {
        if (!evt?.data || typeof evt.data !== 'object') return;
        if (evt.origin !== 'https://cashy.link') return;
        const { type, email, refresh_token } = evt.data || {};
        if (type === 'cashy_oauth_token' && user?.uid && email && refresh_token) {
          const ok = await saveGoogleBackupToken(user.uid, String(email), String(refresh_token));
          if (ok) {
            toast({ title: 'تم ربط جوجل', description: 'تم حفظ توكن النسخ الاحتياطي عبر الخادم.' });
          }
        }
      } catch (e) { void e; }
    };
    window.addEventListener('message', handler);
    return () => window.removeEventListener('message', handler);
  }, [user?.uid]);

  if (!user) {
    return null;
  }

  const handleSignOut = async () => {
    try {
      await signOut();
    } catch (error) {
      console.error("خطأ في تسجيل الخروج:", error);
    }
  };

  const handleBackup = async () => {
    if (!user?.uid) return;
    try {
      setIsBackingUp(true);
      // إذا لا يوجد توكن، نفّذ تسجيل الدخول أولاً ثم أكمل النسخ
      if (!googleToken) {
        // جرّب أولاً تدفق الخادم لربط حساب جوجل وحفظ refresh_token
        const opened = await startServerGoogleOauth();
        if (opened) {
          const linked = await pollForServerGoogleToken(user.uid, 20000, 1500);
          if (!linked) {
            // لم يتم العثور على التوكن من الخادم خلال المهلة — جرّب GIS ثم Firebase
            const signRes = await signInWithGoogleForBackupGis('483702062341-bhdprmoot8rto79jmjvkijdl7pbz28b.apps.googleusercontent.com');
            if (!signRes.ok) {
              const fbRes = await signInWithGoogleForBackup(auth);
              if (!fbRes.ok) {
                toast({ title: 'فشل تسجيل الدخول', description: fbRes.error || signRes.error || 'يرجى التسجيل بجوجل أولاً.', variant: 'destructive' });
                return;
              }
            }
            setGoogleTokenInfo(getGoogleAccessTokenInfo());
          }
        } else {
          // تعذّر فتح نافذة الموافقة — جرّب GIS ثم Firebase مباشرة
          const signRes = await signInWithGoogleForBackupGis('483702062341-bhdprmoot8rto79jmjvkijdl7pbz28b.apps.googleusercontent.com');
          if (!signRes.ok) {
            const fbRes = await signInWithGoogleForBackup(auth);
            if (!fbRes.ok) {
              toast({ title: 'فشل تسجيل الدخول', description: fbRes.error || signRes.error || 'يرجى التسجيل بجوجل أولاً.', variant: 'destructive' });
              return;
            }
          }
          setGoogleTokenInfo(getGoogleAccessTokenInfo());
        }
      }
      const res = await backupToGoogleDriveWithUiToken(user.uid);
      if (res.ok) {
        toast({ title: 'تم النسخ الاحتياطي', description: 'تم حفظ بياناتك إلى Google Drive بنجاح.' });
      } else {
        toast({ title: 'فشل النسخ الاحتياطي', description: res.error || 'حدث خطأ أثناء النسخ الاحتياطي.', variant: 'destructive' });
      }
    } catch (e) {
      toast({ title: 'خطأ غير متوقع', description: 'تعذر إكمال النسخ الاحتياطي.', variant: 'destructive' });
    } finally {
      setIsBackingUp(false);
    }
  };

  const handleRestore = async () => {
    if (!user?.uid) return;
    try {
      setIsRestoring(true);
      // إذا لا يوجد توكن، نفّذ تسجيل الدخول أولاً ثم أكمل الاسترجاع
      if (!googleToken) {
        const opened = await startServerGoogleOauth();
        if (opened) {
          const linked = await pollForServerGoogleToken(user.uid, 20000, 1500);
          if (!linked) {
            const signRes = await signInWithGoogleForBackupGis('483702062341-bhdprmoot8rto79jmjvkijdl7pbz28b.apps.googleusercontent.com');
            if (!signRes.ok) {
              const fbRes = await signInWithGoogleForBackup(auth);
              if (!fbRes.ok) {
                toast({ title: 'فشل تسجيل الدخول', description: fbRes.error || signRes.error || 'يرجى التسجيل بجوجل أولاً.', variant: 'destructive' });
                return;
              }
            }
            setGoogleTokenInfo(getGoogleAccessTokenInfo());
          }
        } else {
          const signRes = await signInWithGoogleForBackupGis('483702062341-bhdprmoot8rto79jmjvkijdl7pbz28b.apps.googleusercontent.com');
          if (!signRes.ok) {
            const fbRes = await signInWithGoogleForBackup(auth);
            if (!fbRes.ok) {
              toast({ title: 'فشل تسجيل الدخول', description: fbRes.error || signRes.error || 'يرجى التسجيل بجوجل أولاً.', variant: 'destructive' });
              return;
            }
          }
          setGoogleTokenInfo(getGoogleAccessTokenInfo());
        }
      }
      const res = await restoreFromGoogleDriveWithUiToken(user.uid);
      if (res.ok) {
        toast({ title: 'تم الاسترجاع', description: 'تم استرجاع بياناتك بنجاح من Google Drive.' });
      } else {
        toast({ title: 'فشل الاسترجاع', description: res.error || 'حدث خطأ أثناء الاسترجاع.', variant: 'destructive' });
      }
    } catch (e) {
      toast({ title: 'خطأ غير متوقع', description: 'تعذر إكمال الاسترجاع.', variant: 'destructive' });
    } finally {
      setIsRestoring(false);
    }
  };

  const handleGoogleSignIn = async () => {
    try {
      setIsGoogleSigning(true);
      // ابدأ أولاً تدفق الخادم وفق إعداداتك (start → callback)
      const opened = await startServerGoogleOauth();
      let linked = false;
      if (opened && user?.uid) {
        linked = await pollForServerGoogleToken(user.uid, 20000, 1500);
      }
      if (linked) {
        toast({ title: 'تم ربط جوجل', description: `تم تفعيل النسخ الاحتياطي عبر الخادم` });
        return;
      }

      // إن لم يكتمل ربط السيرفر، جرّب GIS ثم Firebase
      const res = await signInWithGoogleForBackupGis('483702062341-bhdprmoot8rto79jmjvkijdl7pbz28b.apps.googleusercontent.com');
      if (res.ok) {
        const info = getGoogleAccessTokenInfo();
        setGoogleTokenInfo(info);
        toast({ title: 'تم ربط جوجل', description: `تم تفعيل النسخ الاحتياطي على Google Drive` });
      } else {
        const fb = await signInWithGoogleForBackup(auth);
        if (fb.ok) {
          const info = getGoogleAccessTokenInfo();
          setGoogleTokenInfo(info);
          toast({ title: 'تم ربط جوجل', description: `تم تفعيل النسخ الاحتياطي على Google Drive` });
        } else {
          toast({ title: 'فشل ربط جوجل', description: fb.error || res.error || 'حدث خطأ أثناء تسجيل الدخول بجوجل.', variant: 'destructive' });
        }
      }
    } catch (e) {
      toast({ title: 'خطأ غير متوقع', description: 'تعذر إكمال تسجيل الدخول بجوجل.', variant: 'destructive' });
    } finally {
      setIsGoogleSigning(false);
    }
  };

  return (
    <>
      <div className="flex items-center gap-2">
        {/* تم إخفاء أيقونة النسخ الاحتياطي على جوجل */}

        {isElectron && (
          <Button
            type="button"
            onClick={() => {
              setUpdateDialogOpen(true);
              if (compareVersions(updateVersion, currentVersion) > 0) {
                setUpdateAvailable(true);
              }
            }}
            title="تحديث التطبيق"
            className="relative inline-flex items-center justify-center w-9 h-9 rounded-md bg-transparent text-blue-600 hover:bg-blue-500/10 focus:outline-none dark:text-blue-400 dark:hover:bg-blue-400/10"
            variant="ghost"
          >
            <DownloadCloud className="w-5 h-5" />
            {updateAvailable && (
              <span className="absolute -top-0.5 -right-0.5 inline-block w-2 h-2 rounded-full bg-red-500"></span>
            )}
          </Button>
        )}

        <DropdownMenu>
          <DropdownMenuTrigger asChild>
            <Button
              variant="ghost"
              disabled={isShiftActive}
              title={isShiftActive ? "غير متاح أثناء وجود وردية كاشير نشطة" : undefined}
              className={`relative h-8 w-8 rounded-full p-0 hover:bg-transparent focus:ring-1 focus:ring-primary focus:ring-offset-1 transition-all duration-200 disabled:opacity-60 disabled:cursor-not-allowed ${className}`}
            >
              <Avatar className="h-8 w-8 border border-border shadow-sm hover:shadow-md transition-all duration-200 hover:scale-105 bg-white">
                {selectedAvatar ? (
                  <div className="w-full h-full flex items-center justify-center bg-white dark:bg-neutral-900/20 rounded-full overflow-hidden">
                    <div className="w-full h-full flex items-center justify-center scale-[2.5]">
                      {selectedAvatar.component}
                    </div>
                  </div>
                ) : (
                  <>
                    <AvatarImage
                      src={user.photoURL || undefined}
                      alt={profile?.fullName || user.email || "المستخدم"}
                      className="object-cover"
                    />
                    <AvatarFallback className="bg-white text-gray-900 font-semibold text-lg border-0">
                      {getInitials()}
                    </AvatarFallback>
                  </>
                )}
              </Avatar>
            </Button>
          </DropdownMenuTrigger>
          <DropdownMenuContent className="w-64" align="end" forceMount>
          <DropdownMenuLabel className="font-normal">
            <div className="flex flex-col space-y-1">
              <p className="text-sm font-medium leading-none">
                {profile?.fullName || "المستخدم"}
              </p>
              <p className="text-xs leading-none text-muted-foreground">
                {user.email}
              </p>
              {profile?.role && (
                <p className="text-xs leading-none text-muted-foreground">
                  الدور: {profile.role === "staff" ? "مدير" : profile.role === "merchant" ? "تاجر" : "مستخدم"}
                </p>
              )}
              {isOnFreeTrial && (
                 <div className="mt-2 flex items-center justify-center gap-1 bg-emerald-50 border border-emerald-200 text-emerald-800 text-xs rounded-md py-1">
                   <span className="font-semibold">تجربة: {freeTrialHoursRemaining} {freeTrialHoursRemaining >= 3 && freeTrialHoursRemaining <= 10 ? 'ساعات' : freeTrialHoursRemaining === 2 ? 'ساعتين' : 'ساعة'}</span>
                 </div>
              )}
            </div>
          </DropdownMenuLabel>

          <DropdownMenuSeparator />
          <DropdownMenuItem 
            onClick={() => navigate('/profile-settings')}
            className="cursor-pointer"
          >
            <Settings className="mr-2 h-4 w-4" />
            <span>إعدادات البروفيل</span>
          </DropdownMenuItem>
          {(profile?.role === "admin" || profile?.role === "staff") && (
            <DropdownMenuItem 
              onClick={() => navigate('/admin')}
              className="cursor-pointer"
            >
              <Shield className="mr-2 h-4 w-4" />
              <span>لوحة الإدارة</span>
            </DropdownMenuItem>
          )}
          <DropdownMenuSeparator />
          <DropdownMenuItem
            onClick={handleSignOut}
            className="cursor-pointer text-red-600 focus:text-red-600"
          >
            <LogOut className="mr-2 h-4 w-4" />
            <span>تسجيل الخروج</span>
          </DropdownMenuItem>
          </DropdownMenuContent>
        </DropdownMenu>
      </div>

      {isElectron && (
        <Dialog open={updateDialogOpen} onOpenChange={(open) => {
          setUpdateDialogOpen(open);
          if (!open) {
            const uidVal = String(user?.uid || 'anon');
            const seenKey = `desktop_update_seen_${uidVal}`;
            try { if (updateVersion) localStorage.setItem(seenKey, updateVersion); } catch {}
            setUpdateAvailable(false);
          }
        }}>
          <DialogContent className="sm:max-w-md">
            <DialogHeader>
              <DialogTitle>يوجد تحديث جديد للبرنامج</DialogTitle>
            </DialogHeader>
            <div className="text-sm">
              {isDownloadingUpdate
                ? 'جارٍ تنزيل التحديث...'
                : (updateAvailable && compareVersions(updateVersion, currentVersion) > 0
                    ? 'هل ترغب في تنزيله الآن؟'
                    : 'التطبيق مُحدَّث لآخر إصدار')}
            </div>
            {updateVersion && (
              <div className="mt-2 text-xs">الإصدار الجديد: <span className="font-semibold">v{updateVersion}</span></div>
            )}
            {currentVersion && (
              <div className="mt-1 text-xs text-muted-foreground">إصدار التطبيق لديك: <span className="font-semibold">v{currentVersion}</span></div>
            )}
            {updateNotes && (
              <div className="mt-2 text-xs text-muted-foreground whitespace-pre-line">{updateNotes}</div>
            )}
            {isDownloadingUpdate && (
              <div className="mt-3">
                <div className="w-full h-2 bg-gray-200 rounded">
                  <div className="h-2 bg-blue-600 rounded" style={{ width: `${updateProgress}%` }} />
                </div>
                <div className="mt-1 text-xs text-muted-foreground">{Math.round(updateProgress)}%</div>
              </div>
            )}
            <div className="flex items-center justify-end gap-2 mt-3">
              <Button variant="outline" onClick={() => {
                const uidVal = String(user?.uid || 'anon');
                const seenKey = `desktop_update_seen_${uidVal}`;
                try { if (updateVersion) localStorage.setItem(seenKey, updateVersion); } catch {}
                setUpdateAvailable(false);
                setUpdateDialogOpen(false);
              }}>إلغاء</Button>
              {!isDownloadingUpdate && updateProgress < 100 && updateAvailable && compareVersions(updateVersion, currentVersion) > 0 && (
                <Button className="bg-blue-600 hover:bg-blue-700" onClick={handleConfirmUpdate}>بدء التنزيل</Button>
              )}
              {updateProgress >= 100 && (
                <Button
                  className="bg-green-600 hover:bg-green-700"
                  onClick={async () => {
                    try {
                      if (electronAPI?.openUpdateFolder) {
                        const res = await electronAPI.openUpdateFolder();
                        if (typeof res === 'string' && res.startsWith('err:')) {
                          toast({ title: 'تعذر فتح مجلد التحديث', description: res.slice(4), variant: 'destructive' });
                        } else {
                          toast({ title: 'افتح ملف التثبيت', description: 'تم فتح مجلد التحديث — اضغط على الملف لتثبيت.' });
                        }
                      } else {
                        await handleApplyUpdate();
                      }
                    } catch (e) {
                      toast({ title: 'تعذر فتح مجلد التحديث', description: (e as any)?.message || 'حدث خطأ غير متوقع', variant: 'destructive' });
                    }
                  }}
                >تحديث الآن</Button>
              )}
            </div>
          </DialogContent>
        </Dialog>
      )}

      <ChangePasswordDialog
        open={showChangePassword}
        onOpenChange={setShowChangePassword}
      />
    </>
  );
}

export default ProfileIcon;
