import { Capacitor } from '@capacitor/core';
import { userDataService, merchantWalletService, dailyReportsArchiveService } from '@/integrations/firebase/services';
import { GoogleAuthProvider, OAuthCredential, signInWithPopup, type Auth } from 'firebase/auth';

export interface BackupResult {
  ok: boolean;
  error?: string;
}

// ====== تخزين واسترجاع توكن جوجل على الواجهة ======
const GOOGLE_TOKEN_KEY = 'cashy.google.accessToken';
const GOOGLE_EMAIL_KEY = 'cashy.google.email';
const GOOGLE_EXPIRES_KEY = 'cashy.google.expiresAt';

function setGoogleAccessToken(token: string, email?: string, expiresAtSeconds?: number) {
  try {
    localStorage.setItem(GOOGLE_TOKEN_KEY, token);
    if (email) localStorage.setItem(GOOGLE_EMAIL_KEY, email);
    const exp = expiresAtSeconds ?? Math.floor(Date.now() / 1000) + 3500; // ~58min
    localStorage.setItem(GOOGLE_EXPIRES_KEY, String(exp));
  } catch {}
}

export function getGoogleAccessTokenInfo(): { token: string | null; email: string | null; expiresAt: number | null } {
  try {
    const token = localStorage.getItem(GOOGLE_TOKEN_KEY);
    const email = localStorage.getItem(GOOGLE_EMAIL_KEY);
    const expiresAtStr = localStorage.getItem(GOOGLE_EXPIRES_KEY);
    const expiresAt = expiresAtStr ? Number(expiresAtStr) : null;
    return { token, email, expiresAt };
  } catch {
    return { token: null, email: null, expiresAt: null };
  }
}

function isGoogleTokenValid(): boolean {
  const { token, expiresAt } = getGoogleAccessTokenInfo();
  if (!token) return false;
  if (!expiresAt) return true;
  return Math.floor(Date.now() / 1000) < expiresAt - 30; // 30s margin
}

export async function signInWithGoogleForBackup(auth: Auth): Promise<{ ok: boolean; email?: string; error?: string }> {
  try {
    const provider = new GoogleAuthProvider();
    provider.addScope('https://www.googleapis.com/auth/drive.file');
    provider.addScope('https://www.googleapis.com/auth/userinfo.email');
    provider.setCustomParameters({ prompt: 'consent', access_type: 'offline', include_granted_scopes: 'true' });

    const result = await signInWithPopup(auth, provider);
    const credential = GoogleAuthProvider.credentialFromResult(result) as OAuthCredential | null;
    const accessToken = credential?.accessToken || null;
    const email = result.user?.email || undefined;
    if (!accessToken) {
      return { ok: false, error: 'تعذر الحصول على توكن الوصول من جوجل' };
    }
    setGoogleAccessToken(accessToken, email);
    return { ok: true, email };
  } catch (e: any) {
    const message = e?.message || 'فشل تسجيل الدخول بجوجل';
    return { ok: false, error: message };
  }
}

// ====== Google Identity Services (GIS) للحصول على access token باستخدام Client ID مخصص ======
function loadGisScript(): Promise<void> {
  return new Promise((resolve, reject) => {
    if ((window as any).google?.accounts?.oauth2) {
      resolve();
      return;
    }
    const s = document.createElement('script');
    s.src = 'https://accounts.google.com/gsi/client';
    s.async = true;
    s.onload = () => resolve();
    s.onerror = () => reject(new Error('فشل تحميل مكتبة Google Identity Services'));
    document.head.appendChild(s);
  });
}

export async function signInWithGoogleForBackupGis(clientId: string): Promise<{ ok: boolean; email?: string; error?: string }> {
  try {
    await loadGisScript();
    const googleAny = (window as any).google;
    if (!googleAny?.accounts?.oauth2) return { ok: false, error: 'مكتبة Google غير متاحة' };

    const scopes = 'https://www.googleapis.com/auth/drive.file https://www.googleapis.com/auth/userinfo.email';
    const tokenPromise = new Promise<string>((resolve, reject) => {
      const client = googleAny.accounts.oauth2.initTokenClient({
        client_id: clientId,
        scope: scopes,
        callback: (resp: any) => {
          if (resp?.access_token) resolve(String(resp.access_token));
          else reject(new Error('تعذر الحصول على access token'));
        },
        prompt: 'consent',
      });
      // أطلق الطلب وحضّر مهلة زمنية لضمان عدم التعليق في حال ظهور صفحة خطأ من جوجل
      try { client.requestAccessToken({ prompt: 'consent' }); } catch (e) { reject(e); }

      const timeoutMs = 12000; // 12 ثوانٍ مهلة كافية لالتقاط الأخطاء
      const timer = setTimeout(() => {
        reject(new Error('انتهت المهلة أثناء طلب رمز الوصول من جوجل'));
      }, timeoutMs);

      // نظّف المؤقت عند الحسم
      const clear = (fn: Function) => (value: any) => { clearTimeout(timer); fn(value); };
      // التفاف resolve/reject لضمان تنظيف المؤقت
      (resolve as any) = clear(resolve);
      (reject as any) = clear(reject);
    });

    const accessToken = await tokenPromise;
    // جلب البريد من userinfo.email
    let email: string | undefined = undefined;
    try {
      const resp = await fetch('https://www.googleapis.com/oauth2/v3/userinfo', {
        headers: { Authorization: `Bearer ${accessToken}` },
      });
      const data = await resp.json().catch(() => ({}));
      if (data?.email) email = String(data.email);
    } catch {}

    setGoogleAccessToken(accessToken, email);
    return { ok: true, email };
  } catch (e: any) {
    const msg = e?.message || 'فشل تسجيل الدخول بجوجل';
    return { ok: false, error: msg };
  }
}

// بدء تدفق OAuth عبر خادم التطبيق
export async function startServerGoogleOauth(baseUrl?: string): Promise<boolean> {
  try {
    // لضمان تطابق redirect_uri تمامًا مع إعداد Google Cloud
    // نستخدم دومين الإنتاج مباشرة: https://cashy.link/api/google/oauth2/start
    const target = 'https://cashy.link/api/google/oauth2/start';
    const w = window.open(target, 'cashy_google_oauth', 'width=600,height=650,menubar=no,toolbar=no');
    return !!w;
  } catch {
    return false;
  }
}

// انتظار توفر توكن السيرفر بعد إتمام الموافقة
export async function pollForServerGoogleToken(userId?: string, timeoutMs = 20000, intervalMs = 1500): Promise<boolean> {
  const start = Date.now();
  while (Date.now() - start < timeoutMs) {
    const t = await fetchStoredRefreshToken(userId);
    if (t && t.refresh_token) return true;
    await new Promise(r => setTimeout(r, intervalMs));
  }
  return false;
}

export async function fetchStoredRefreshToken(userId?: string): Promise<{ refresh_token: string; email: string } | null> {
  // نحاول أولاً نقطة نهاية الإنتاج لضمان التطابق مع redirect_uri
  try {
    const controller = new AbortController();
    const timeoutId = setTimeout(() => controller.abort(), 6000); // 6s مهلة سريعة لتجنب التعليق
    const resp = await fetch('https://cashy.link/api/google/oauth2/token', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        ...(userId ? { 'X-USER-ID': userId } : {}),
      },
      credentials: 'include',
      signal: controller.signal,
    }).finally(() => clearTimeout(timeoutId));
    const data = await resp.json().catch(() => ({}));
    if (resp.ok && data?.ok && data?.refresh_token && data?.email) {
      return { refresh_token: String(data.refresh_token), email: String(data.email) };
    }
  } catch {}

  // مسارات محلية احتياطية إن كانت متاحة
  for (const url of ['/api/google/oauth2/token', '/user/google-backup-token']) {
    try {
      const controller = new AbortController();
      const timeoutId = setTimeout(() => controller.abort(), 5000);
      const resp = await fetch(url, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          ...(userId ? { 'X-USER-ID': userId } : {}),
        },
        signal: controller.signal,
      }).finally(() => clearTimeout(timeoutId));
      const data = await resp.json().catch(() => ({}));
      if (resp.ok && data?.ok && data?.refresh_token && data?.email) {
        return { refresh_token: String(data.refresh_token), email: String(data.email) };
      }
    } catch {}
  }
  return null;
}

async function exchangeRefreshToken(refreshToken: string): Promise<string | null> {
  try {
    const clientId = String((import.meta as any)?.env?.VITE_GOOGLE_OAUTH_CLIENT_ID || '').trim();
    const clientSecret = String((import.meta as any)?.env?.VITE_GOOGLE_OAUTH_CLIENT_SECRET || '').trim();
    const body = new URLSearchParams({
      grant_type: 'refresh_token',
      refresh_token: refreshToken,
      client_id: clientId,
    });
    if (clientSecret) body.set('client_secret', clientSecret);
    const resp = await fetch('https://oauth2.googleapis.com/token', {
      method: 'POST',
      headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
      body: body.toString(),
    });
    const json = await resp.json().catch(() => ({}));
    return json?.access_token ? String(json.access_token) : null;
  } catch {
    return null;
  }
}

async function ensureAppFolder(accessToken: string): Promise<string | null> {
  const headers = { Authorization: `Bearer ${accessToken}` };
  const q = encodeURIComponent("name='Cashy Backup' and mimeType='application/vnd.google-apps.folder' and trashed=false");
  const listUrl = `https://www.googleapis.com/drive/v3/files?q=${q}&fields=files(id,name)`;
  const resp = await fetch(listUrl, { headers });
  const data = await resp.json().catch(() => ({}));
  const existing = Array.isArray(data?.files) ? data.files[0] : null;
  if (existing?.id) return String(existing.id);
  const createResp = await fetch('https://www.googleapis.com/drive/v3/files', {
    method: 'POST',
    headers: { ...headers, 'Content-Type': 'application/json' },
    body: JSON.stringify({ name: 'Cashy Backup', mimeType: 'application/vnd.google-apps.folder' }),
  });
  const created = await createResp.json().catch(() => ({}));
  return created?.id ? String(created.id) : null;
}

async function findLatestFileInFolder(accessToken: string, parentFolderId: string): Promise<{ id: string; name: string } | null> {
  const headers = { Authorization: `Bearer ${accessToken}` };
  const q = encodeURIComponent(`'${parentFolderId}' in parents and trashed=false`);
  const url = `https://www.googleapis.com/drive/v3/files?q=${q}&orderBy=modifiedTime desc&fields=files(id,name,modifiedTime,createdTime)`;
  const resp = await fetch(url, { headers });
  const data = await resp.json().catch(() => ({}));
  const f = Array.isArray(data?.files) ? data.files[0] : null;
  if (f?.id) return { id: String(f.id), name: String(f.name) };
  return null;
}

async function uploadJsonFile(accessToken: string, parentFolderId: string, filename: string, content: any): Promise<boolean> {
  const metadata = { name: filename, parents: [parentFolderId] };
  const boundary = 'foo_bar_baz_' + Math.random().toString(16).slice(2);
  const delimiter = `\r\n--${boundary}\r\n`;
  const closeDelim = `\r\n--${boundary}--`;
  const body =
    delimiter +
    'Content-Type: application/json; charset=UTF-8\r\n\r\n' +
    JSON.stringify(metadata) +
    delimiter +
    'Content-Type: application/json\r\n\r\n' +
    JSON.stringify(content) +
    closeDelim;
  const resp = await fetch('https://www.googleapis.com/upload/drive/v3/files?uploadType=multipart', {
    method: 'POST',
    headers: {
      Authorization: `Bearer ${accessToken}`,
      'Content-Type': `multipart/related; boundary=${boundary}`,
    },
    body,
  });
  return resp.ok;
}

async function findFile(accessToken: string, parentFolderId: string, name: string): Promise<string | null> {
  const headers = { Authorization: `Bearer ${accessToken}` };
  const q = encodeURIComponent(`name='${name}' and '${parentFolderId}' in parents and trashed=false`);
  const url = `https://www.googleapis.com/drive/v3/files?q=${q}&fields=files(id,name)`;
  const resp = await fetch(url, { headers });
  const data = await resp.json().catch(() => ({}));
  const f = Array.isArray(data?.files) ? data.files[0] : null;
  return f?.id ? String(f.id) : null;
}

async function downloadFileJson(accessToken: string, fileId: string): Promise<any | null> {
  const url = `https://www.googleapis.com/drive/v3/files/${fileId}?alt=media`;
  const resp = await fetch(url, { headers: { Authorization: `Bearer ${accessToken}` } });
  if (!resp.ok) return null;
  const text = await resp.text();
  try { return JSON.parse(text); } catch { return null; }
}

function collectLocalBackupData(): any {
  const data: Record<string, any> = {};
  try {
    for (let i = 0; i < localStorage.length; i++) {
      const key = localStorage.key(i);
      if (!key) continue;
      const val = localStorage.getItem(key);
      data[key] = val;
    }
  } catch {}
  return { platform: Capacitor.getPlatform(), ts: new Date().toISOString(), localStorage: data };
}

function restoreLocalBackupData(content: any): boolean {
  try {
    const obj = content?.localStorage;
    if (obj && typeof obj === 'object') {
      for (const [k, v] of Object.entries(obj)) {
        localStorage.setItem(String(k), String(v ?? ''));
      }
      return true;
    }
  } catch {}
  return false;
}

export async function backupToGoogleDrive(userId?: string): Promise<BackupResult> {
  // أولاً جرّب التوكن المخزّن في الواجهة (تسجيل الدخول بجوجل عبر Firebase)
  let accessToken: string | null = null;
  if (isGoogleTokenValid()) {
    accessToken = getGoogleAccessTokenInfo().token;
  }
  // إن لم يكن متاحاً، نعود للتوكن المخزن بالخادم (في حال تم ربط سابقاً)
  if (!accessToken) {
    const stored = await fetchStoredRefreshToken(userId);
    if (!stored) return { ok: false, error: 'لا يوجد ربط بحساب جوجل. يرجى تسجيل الدخول بجوجل أولاً.' };
    accessToken = await exchangeRefreshToken(stored.refresh_token);
  }
  if (!accessToken) return { ok: false, error: 'فشل إنشاء access token' };
  const folderId = await ensureAppFolder(accessToken);
  if (!folderId) return { ok: false, error: 'فشل إنشاء/إيجاد مجلد التطبيق' };
  const content = await collectFullBackupData(userId);
  const tsName = `cashy-backup-${new Date().toISOString().replace(/[.:]/g, '-')}.json`;
  const ok = await uploadJsonFile(accessToken, folderId, tsName, content);
  return ok ? { ok: true } : { ok: false, error: 'فشل رفع الملف' };
}

export async function restoreFromGoogleDrive(userId?: string): Promise<BackupResult> {
  // أولاً جرّب التوكن المخزّن في الواجهة
  let accessToken: string | null = null;
  if (isGoogleTokenValid()) {
    accessToken = getGoogleAccessTokenInfo().token;
  }
  // إن لم يكن متاحاً، نعود للتوكن المخزن بالخادم
  if (!accessToken) {
    const stored = await fetchStoredRefreshToken(userId);
    if (!stored) return { ok: false, error: 'لا يوجد ربط بحساب جوجل. يرجى تسجيل الدخول بجوجل أولاً.' };
    accessToken = await exchangeRefreshToken(stored.refresh_token);
  }
  if (!accessToken) return { ok: false, error: 'فشل إنشاء access token' };
  const folderId = await ensureAppFolder(accessToken);
  if (!folderId) return { ok: false, error: 'فشل إيجاد مجلد التطبيق' };
  const latest = await findLatestFileInFolder(accessToken, folderId);
  if (!latest?.id) return { ok: false, error: 'لا توجد نسخة احتياطية' };
  const json = await downloadFileJson(accessToken, latest.id);
  if (!json) return { ok: false, error: 'فشل تنزيل النسخة' };
  const okLocal = restoreLocalBackupData(json);
  const okFirestore = await restoreFullBackupData(userId, json);
  return okLocal && okFirestore ? { ok: true } : { ok: false, error: 'فشل الاسترجاع' };
}

// إصدارات صريحة تعتمد فقط على التوكن المخزن في الواجهة
export async function backupToGoogleDriveWithUiToken(userId?: string): Promise<BackupResult> {
  if (!isGoogleTokenValid()) return { ok: false, error: 'يرجى تسجيل الدخول بجوجل أولاً' };
  const token = getGoogleAccessTokenInfo().token!;
  const folderId = await ensureAppFolder(token);
  if (!folderId) return { ok: false, error: 'فشل إنشاء/إيجاد مجلد التطبيق' };
  const content = await collectFullBackupData(userId);
  const tsName = `cashy-backup-${new Date().toISOString().replace(/[.:]/g, '-')}.json`;
  const ok = await uploadJsonFile(token, folderId, tsName, content);
  return ok ? { ok: true } : { ok: false, error: 'فشل رفع الملف' };
}

export async function restoreFromGoogleDriveWithUiToken(userId?: string): Promise<BackupResult> {
  if (!isGoogleTokenValid()) return { ok: false, error: 'يرجى تسجيل الدخول بجوجل أولاً' };
  const token = getGoogleAccessTokenInfo().token!;
  const folderId = await ensureAppFolder(token);
  if (!folderId) return { ok: false, error: 'فشل إيجاد مجلد التطبيق' };
  const latest = await findLatestFileInFolder(token, folderId);
  if (!latest?.id) return { ok: false, error: 'لا توجد نسخة احتياطية' };
  const json = await downloadFileJson(token, latest.id);
  if (!json) return { ok: false, error: 'فشل تنزيل النسخة' };
  const okLocal = restoreLocalBackupData(json);
  const okFirestore = await restoreFullBackupData(userId, json);
  return okLocal && okFirestore ? { ok: true } : { ok: false, error: 'فشل الاسترجاع' };
}

export async function saveGoogleBackupToken(userId: string, email: string, refreshToken: string): Promise<boolean> {
  try {
    const resp = await fetch('/user/google-backup-token', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json', 'X-USER-ID': userId },
      body: JSON.stringify({ email, refresh_token: refreshToken }),
    });
    const data = await resp.json().catch(() => ({}));
    return !!(resp.ok && data?.ok);
  } catch {
    return false;
  }
}

// ====== دعم النسخ الاحتياطي/الاسترجاع لبيانات Firestore (المحافظ – المعاملات – الديون – العملاء – التقارير) ======

function serializeDate(val: any): any {
  if (val instanceof Date) return val.toISOString();
  return val;
}

function deepSerializeDates(obj: any): any {
  if (obj === null || obj === undefined) return obj;
  if (Array.isArray(obj)) return obj.map(deepSerializeDates);
  if (typeof obj === 'object') {
    const out: Record<string, any> = {};
    for (const [k, v] of Object.entries(obj)) {
      if (v instanceof Date) out[k] = serializeDate(v);
      else out[k] = deepSerializeDates(v);
    }
    return out;
  }
  return obj;
}

async function collectFullBackupData(userId?: string): Promise<any> {
  // في حال عدم توفر userId، نوفر نسخة احتياطية محلية فقط
  if (!userId) {
    return {
      version: 2,
      ts: new Date().toISOString(),
      userId: null,
      local: collectLocalBackupData(),
    };
  }

  try {
    const [userData, wallets, transactions, reports] = await Promise.all([
      userDataService.getUserData(userId),
      merchantWalletService.getByMerchantId(userId),
      userDataService.getUserTransactions(userId),
      dailyReportsArchiveService.getByUser(userId, 500),
    ]);

    return {
      version: 2,
      ts: new Date().toISOString(),
      userId,
      local: collectLocalBackupData(),
      firestore: {
        userData: deepSerializeDates(userData),
        wallets: deepSerializeDates(wallets),
        userTransactions: deepSerializeDates(transactions),
        dailyReportsArchive: deepSerializeDates(reports),
      },
    };
  } catch (e) {
    // في حال فشل جمع Firestore، لا نفشل النسخ الاحتياطي بالكامل — نعود للنسخة المحلية
    return {
      version: 2,
      ts: new Date().toISOString(),
      userId,
      local: collectLocalBackupData(),
      firestore: null,
      error: 'تعذر جمع بعض بيانات Firestore',
    };
  }
}

function parseDateMaybe(val: any): any {
  if (typeof val === 'string') {
    const d = new Date(val);
    if (!isNaN(d.getTime())) return d;
  }
  return val;
}

function deepParseDates(obj: any): any {
  if (obj === null || obj === undefined) return obj;
  if (Array.isArray(obj)) return obj.map(deepParseDates);
  if (typeof obj === 'object') {
    const out: Record<string, any> = {};
    for (const [k, v] of Object.entries(obj)) {
      out[k] = v instanceof Date ? v : parseDateMaybe(deepParseDates(v));
    }
    return out;
  }
  return obj;
}

async function restoreFullBackupData(userId: string | undefined, content: any): Promise<boolean> {
  try {
    if (!userId) return false;
    const firestore = content?.firestore;
    const hasFs = firestore && typeof firestore === 'object';
    if (!hasFs) return true; // اكتفِ بالنسخة المحلية

    // 1) استرجاع بيانات المستخدم (تشمل الديون والعملاء والأرصدة والتقارير الملخصة)
    if (firestore.userData) {
      const parsedUserData = deepParseDates(firestore.userData);
      try {
        await userDataService.saveUserData(userId, parsedUserData);
      } catch {}
    }

    // 2) استرجاع المحافظ
    if (Array.isArray(firestore.wallets)) {
      try {
        const existing = await merchantWalletService.getByMerchantId(userId);
        const existingKey = new Set(
          existing.map(w => `${w.provider}|${w.msisdn}`)
        );
        for (const wRaw of firestore.wallets) {
          const w = deepParseDates(wRaw);
          const key = `${w.provider}|${w.msisdn}`;
          if (!existingKey.has(key)) {
            // أنشئ محفظة جديدة للمستخدم الحالي
            await merchantWalletService.create({
              merchantUserId: userId,
              provider: w.provider,
              msisdn: w.msisdn,
              label: w.label ?? undefined,
              isActive: w.isActive ?? true,
              dailyLimit: Number(w.dailyLimit ?? 0),
              monthlyLimit: Number(w.monthlyLimit ?? 0),
              transferLimit: Number(w.transferLimit ?? 200000),
              withdrawLimit: Number(w.withdrawLimit ?? 200000),
              transferUsage: Number(w.transferUsage ?? 0),
              withdrawUsage: Number(w.withdrawUsage ?? 0),
              dailyTransferLimit: w.dailyTransferLimit ?? undefined,
              dailyWithdrawLimit: w.dailyWithdrawLimit ?? undefined,
              defaultTransferCommission: w.defaultTransferCommission ?? null,
              defaultWithdrawCommission: w.defaultWithdrawCommission ?? null,
            } as any);
          }
        }
      } catch {}
    }

    // 3) استرجاع المعاملات (userTransactions) مع منع التكرار عبر الخدمة
    if (Array.isArray(firestore.userTransactions)) {
      try {
        for (const tRaw of firestore.userTransactions) {
          const t = deepParseDates(tRaw);
          await userDataService.saveUserTransaction(userId, t);
        }
      } catch {}
    }

    // 4) استرجاع أرشيف التقارير اليومية مع تخطي الموجود
    if (Array.isArray(firestore.dailyReportsArchive)) {
      try {
        const existingReports = await dailyReportsArchiveService.getByUser(userId, 1000);
        const existKey = new Set(
          existingReports.map(r => `${r.reportDate}|${r.walletId ?? 'null'}`)
        );
        for (const rRaw of firestore.dailyReportsArchive) {
          const r = deepParseDates(rRaw);
          const key = `${r.reportDate}|${r.walletId ?? 'null'}`;
          if (!existKey.has(key)) {
            await dailyReportsArchiveService.add({
              userId,
              reportDate: String(r.reportDate),
              totalTransactions: Number(r.totalTransactions || 0),
              totalAmount: Number(r.totalAmount || 0),
              totalWithdrawn: Number(r.totalWithdrawn || 0),
              totalSent: Number(r.totalSent || 0),
              profit: r.profit !== undefined ? Number(r.profit) : undefined,
              walletId: r.walletId ?? null,
            });
          }
        }
      } catch {}
    }

    return true;
  } catch {
    return false;
  }
}