import React, { useState } from "react";
import { Eye, EyeOff, Loader2 } from "lucide-react";
import { updatePassword, reauthenticateWithCredential, EmailAuthProvider } from "firebase/auth";
import { useFirebaseAuth } from "@/contexts/FirebaseAuthContext";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { useToast } from "@/hooks/use-toast";
import { MasterPinDialog } from "@/components/MasterPinDialog";

interface ChangePasswordDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
}

export function ChangePasswordDialog({ open, onOpenChange }: ChangePasswordDialogProps) {
  const { user } = useFirebaseAuth();
  const { toast } = useToast();
  const [loading, setLoading] = useState(false);
  const [showCurrentPassword, setShowCurrentPassword] = useState(false);
  const [showNewPassword, setShowNewPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [isMasterPinDialogOpen, setIsMasterPinDialogOpen] = useState(false);
  const [isReauthenticated, setIsReauthenticated] = useState(false);
  
  const [formData, setFormData] = useState({
    currentPassword: "",
    newPassword: "",
    confirmPassword: "",
  });

  const [errors, setErrors] = useState({
    currentPassword: "",
    newPassword: "",
    confirmPassword: "",
  });

  const validateForm = () => {
    const newErrors = {
      currentPassword: "",
      newPassword: "",
      confirmPassword: "",
    };

    if (!formData.currentPassword) {
      newErrors.currentPassword = "كلمة المرور الحالية مطلوبة";
    }

    if (!formData.newPassword) {
      newErrors.newPassword = "كلمة المرور الجديدة مطلوبة";
    } else if (formData.newPassword.length < 6) {
      newErrors.newPassword = "كلمة المرور يجب أن تكون 6 أحرف على الأقل";
    }

    if (!formData.confirmPassword) {
      newErrors.confirmPassword = "تأكيد كلمة المرور مطلوب";
    } else if (formData.newPassword !== formData.confirmPassword) {
      newErrors.confirmPassword = "كلمات المرور غير متطابقة";
    }

    setErrors(newErrors);
    return !Object.values(newErrors).some(error => error !== "");
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm() || !user || !user.email) {
      return;
    }

    setLoading(true);

    try {
      // إعادة المصادقة أولاً
      const credential = EmailAuthProvider.credential(user.email, formData.currentPassword);
      await reauthenticateWithCredential(user, credential);

      // افتح حوار الرقم السري الرئيسي للتحقق قبل التنفيذ الفعلي
      setIsReauthenticated(true);
      setIsMasterPinDialogOpen(true);
      toast({
        title: "تأكيد الهوية",
        description: "أدخل الرقم السري الرئيسي لتأكيد تغيير كلمة المرور",
      });

    } catch (error: any) {
      console.error("خطأ في تغيير كلمة المرور:", error);
      
      let errorMessage = "حدث خطأ أثناء تغيير كلمة المرور";
      
      if (error.code === "auth/wrong-password") {
        setErrors(prev => ({ ...prev, currentPassword: "كلمة المرور الحالية غير صحيحة" }));
        return;
      } else if (error.code === "auth/weak-password") {
        setErrors(prev => ({ ...prev, newPassword: "كلمة المرور ضعيفة جداً" }));
        return;
      } else if (error.code === "auth/requires-recent-login") {
        errorMessage = "يجب تسجيل الدخول مرة أخرى لتغيير كلمة المرور";
      }

      toast({
        title: "خطأ في تغيير كلمة المرور",
        description: errorMessage,
        variant: "destructive",
      });
    } finally {
      setLoading(false);
    }
  };

  const performPasswordUpdate = async () => {
    if (!user) return;
    setLoading(true);
    try {
      await updatePassword(user, formData.newPassword);

      toast({
        title: "تم تغيير كلمة المرور بنجاح",
        description: "تم تحديث كلمة المرور الخاصة بك بنجاح",
      });

      // إعادة تعيين النموذج وإغلاق الحوارات
      setFormData({
        currentPassword: "",
        newPassword: "",
        confirmPassword: "",
      });
      setErrors({
        currentPassword: "",
        newPassword: "",
        confirmPassword: "",
      });
      setIsMasterPinDialogOpen(false);
      setIsReauthenticated(false);
      onOpenChange(false);

    } catch (error: any) {
      console.error("خطأ في تحديث كلمة المرور:", error);
      let errorMessage = "حدث خطأ أثناء تغيير كلمة المرور";
      if (error.code === "auth/requires-recent-login") {
        errorMessage = "انتهت جلسة المصادقة. يرجى إعادة إدخال كلمة المرور الحالية";
      }
      toast({ title: "خطأ في تغيير كلمة المرور", description: errorMessage, variant: "destructive" });
    } finally {
      setLoading(false);
    }
  };

  const handleInputChange = (field: keyof typeof formData) => (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData(prev => ({ ...prev, [field]: e.target.value }));
    // مسح الخطأ عند الكتابة
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: "" }));
    }
  };

  const handleClose = () => {
    setFormData({
      currentPassword: "",
      newPassword: "",
      confirmPassword: "",
    });
    setErrors({
      currentPassword: "",
      newPassword: "",
      confirmPassword: "",
    });
    setIsMasterPinDialogOpen(false);
    setIsReauthenticated(false);
    onOpenChange(false);
  };

  return (
    <>
      <Dialog open={open} onOpenChange={handleClose}>
        <DialogContent className="sm:max-w-[425px]">
          <DialogHeader>
            <DialogTitle>تغيير كلمة المرور</DialogTitle>
            <DialogDescription>
              أدخل كلمة المرور الحالية وكلمة المرور الجديدة لتحديث حسابك
            </DialogDescription>
          </DialogHeader>
          
          <form onSubmit={handleSubmit} className="space-y-4">
            <div className="space-y-2">
              <Label htmlFor="currentPassword">كلمة المرور الحالية</Label>
              <div className="relative">
                <Input
                  id="currentPassword"
                  type={showCurrentPassword ? "text" : "password"}
                  value={formData.currentPassword}
                  onChange={handleInputChange("currentPassword")}
                  className={errors.currentPassword ? "border-red-500" : ""}
                  autoComplete="off"
                  disabled={loading}
                />
                <Button
                  type="button"
                  variant="ghost"
                  size="sm"
                  className="absolute right-0 top-0 h-full px-3 py-2 hover:bg-transparent"
                  onClick={() => setShowCurrentPassword(!showCurrentPassword)}
                  disabled={loading}
                >
                  {showCurrentPassword ? (
                    <EyeOff className="h-4 w-4" />
                  ) : (
                    <Eye className="h-4 w-4" />
                  )}
                </Button>
              </div>
              {errors.currentPassword && (
                <p className="text-sm text-red-500">{errors.currentPassword}</p>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="newPassword">كلمة المرور الجديدة</Label>
              <div className="relative">
                <Input
                  id="newPassword"
                  type={showNewPassword ? "text" : "password"}
                  value={formData.newPassword}
                  onChange={handleInputChange("newPassword")}
                  className={errors.newPassword ? "border-red-500" : ""}
                  autoComplete="new-password"
                  disabled={loading}
                />
                <Button
                  type="button"
                  variant="ghost"
                  size="sm"
                  className="absolute right-0 top-0 h-full px-3 py-2 hover:bg-transparent"
                  onClick={() => setShowNewPassword(!showNewPassword)}
                  disabled={loading}
                >
                  {showNewPassword ? (
                    <EyeOff className="h-4 w-4" />
                  ) : (
                    <Eye className="h-4 w-4" />
                  )}
                </Button>
              </div>
              {errors.newPassword && (
                <p className="text-sm text-red-500">{errors.newPassword}</p>
              )}
            </div>

            <div className="space-y-2">
              <Label htmlFor="confirmPassword">تأكيد كلمة المرور الجديدة</Label>
              <div className="relative">
                <Input
                  id="confirmPassword"
                  type={showConfirmPassword ? "text" : "password"}
                  value={formData.confirmPassword}
                  onChange={handleInputChange("confirmPassword")}
                  className={errors.confirmPassword ? "border-red-500" : ""}
                  autoComplete="new-password"
                  disabled={loading}
                />
                <Button
                  type="button"
                  variant="ghost"
                  size="sm"
                  className="absolute right-0 top-0 h-full px-3 py-2 hover:bg-transparent"
                  onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                  disabled={loading}
                >
                  {showConfirmPassword ? (
                    <EyeOff className="h-4 w-4" />
                  ) : (
                    <Eye className="h-4 w-4" />
                  )}
                </Button>
              </div>
              {errors.confirmPassword && (
                <p className="text-sm text-red-500">{errors.confirmPassword}</p>
              )}
            </div>

            <DialogFooter>
              <Button
                type="button"
                variant="outline"
                onClick={handleClose}
                disabled={loading}
              >
                إلغاء
              </Button>
              <Button type="submit" disabled={loading}>
                {loading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                تغيير كلمة المرور
              </Button>
            </DialogFooter>
          </form>
        </DialogContent>
      </Dialog>

      {/* حوار التحقق من الرقم السري الرئيسي */}
      <MasterPinDialog
        open={isMasterPinDialogOpen && isReauthenticated}
        onOpenChange={(open) => {
          setIsMasterPinDialogOpen(open);
          if (!open) setIsReauthenticated(false);
        }}
        mode="verify"
        title="التحقق من الرقم السري الرئيسي"
        description="لا يمكن تغيير كلمة المرور إلا بعد التحقق من الرقم السري الرئيسي"
        onSuccess={performPasswordUpdate}
        onCancel={() => {
          setIsMasterPinDialogOpen(false);
          setIsReauthenticated(false);
        }}
      />
    </>
  );
}